<template>
    <div>
      <input type="file" @change="handleFileInputChange" accept="image/*" />
      <br>
      <button @click="convertImageToExcel">Convert Image to Excel</button>
    </div>
  </template>
  
  <script>
  import * as XLSX from 'xlsx';
  
  export default {
    name: 'ImageToExcel',
    data() {
      return {
        imageFile: null
      };
    },
    methods: {
      handleFileInputChange(event) {
        const file = event.target.files[0];
        if (file) {
          this.imageFile = file;
        } else {
          console.error('No file selected');
        }
      },
      convertImageToExcel() {
        if (!this.imageFile) {
          alert('Please select an image file.');
          return;
        }
  
        const reader = new FileReader();
        reader.onload = (event) => {
          const image = new Image();
          image.onload = () => {
            const canvas = document.createElement('canvas');
            canvas.width = image.width;
            canvas.height = image.height;
  
            const ctx = canvas.getContext('2d');
            ctx.drawImage(image, 0, 0, image.width, image.height);
  
            const imageData = ctx.getImageData(0, 0, image.width, image.height).data;
  
            // Prepare Excel data
            if (XLSX && XLSX.utils) {
              const workbook = XLSX.utils.book_new();
              const sheetName = 'Pixels Data';
              const worksheet = XLSX.utils.aoa_to_sheet([]);
  
              for (let y = 0; y < image.height; y++) {
                for (let x = 0; x < image.width; x++) {
                  const index = (y * image.width + x) * 4;
                  const r = imageData[index];
                  const g = imageData[index + 1];
                  const b = imageData[index + 2];
  
                  XLSX.utils.sheet_add_aoa(worksheet, [[r]], { origin: { r: y * 3, c: x } });
                  XLSX.utils.sheet_add_aoa(worksheet, [[g]], { origin: { r: y * 3 + 1, c: x } });
                  XLSX.utils.sheet_add_aoa(worksheet, [[b]], { origin: { r: y * 3 + 2, c: x } });
                }
              }
  
              XLSX.utils.book_append_sheet(workbook, worksheet, sheetName);
              const excelFile = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
  
              const blob = new Blob([excelFile], { type: 'application/octet-stream' });
              const url = window.URL.createObjectURL(blob);
              const a = document.createElement('a');
              a.style.display = 'none';
              a.href = url;
              a.download = 'image_pixels.xlsx';
              document.body.appendChild(a);
              a.click();
              window.URL.revokeObjectURL(url);
              document.body.removeChild(a);
            } else {
              console.error('XLSX is not properly imported');
            }
          };
  
          image.src = event.target.result;
        };
  
        reader.readAsDataURL(this.imageFile);
      }
    }
  };
  </script>
  
  <style scoped>
  /* Add your custom styles here */
  </style>

